define(function () {
    'use strict';
    var Command = function (editor) {
        this.id = -1;
        this.inMemory = false;
        this.updatable = false;
        this.type = '';
        this.name = '';
        this.editor = editor;
    };
    Command.prototype.toJSON = function () {
        var output = {};
        output.type = this.type;
        output.id = this.id;
        output.name = this.name;
        return output;
    };
    Command.prototype.fromJSON = function (json) {
        this.inMemory = true;
        this.type = json.type;
        this.id = json.id;
        this.name = json.name;
    };
    return Command;
});